import validateNIP from "@/helpers/validation/nip";

export default {
  required: (v) => v.length > 0 || "To pole jest wymagane!",

  email: (v) =>
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
      v
    ) || "To jest nieprawidowy adres e-mail!",

  passwordLength: (v) =>
    v.length >= 8 || "Haso powinno mie 8 liter dugoi!",

  numeric: (v) => !isNaN(v) || "To pole powinno zawiera tylko cyfry!",

  zipCode: (v) => v.length > 0 || "To pole nie moe by puste!",

  phone: (v) =>
    !v
      ? true
      : /^\+{0,1}([0-9]{9,20})$/.test(v) ||
        "Numer telefonu powinien mie dugo od 9 do 20 liczb oraz nie zawiera spacji!",

  nip: validateNIP,
};
